import React from 'react';

import { Container } from './styles';
import Menu from '../../components/Menu';
import avatar from '../../assets/avatar.jpg';

function About() {
  return (
    <>
      <Menu />
      <Container>
        <form>
          <h1>Bem vindo ao meu portfolio</h1>
          <table>
            <tbody>
              <tr>
                <td className="avatar">
                  <img src={avatar} alt="avatar" />
                </td>
              </tr>
              <tr>
                <td>Meu nome: Henrique Hoinacki</td>
              </tr>
              <tr>
                <td>Idade: 25 anos</td>
              </tr>
              <tr>
                <td>Minha localidade: Pato Branco PR </td>
              </tr>

              <tr>
                <td>Descrição:</td>
              </tr>
              <tr>
                <td>
                  Estudo linguagem de programação a mais de um ano, atualmente
                  trabalho como programador e estou cursando faculdade de
                  análise e desenvolvimento de sistemas.
                </td>
              </tr>
            </tbody>
          </table>
        </form>
      </Container>
    </>
  );
}

export default About;
