import React from 'react';

import { Container } from './styles';
import Menu from '../../components/Menu';
import { FaFilePdf } from 'react-icons/fa';

function Home() {
  return (
    <>
      <Menu />
      <Container>
        <form>
          <h1>Meu Currículo</h1>
          <table>
            <tbody>
              <tr>
                <td>
                  <a href="https://drive.google.com/file/d/1LfA_nl-QMyas2R7vd-tXEaPiZlnIqGA2/view?usp=sharing">
                    Download do meu currículo
                  </a>
                  <FaFilePdf color="#FE2E2E" />
                </td>
              </tr>
            </tbody>
          </table>
        </form>
      </Container>
    </>
  );
}

export default Home;
