import styled from 'styled-components';

export const Container = styled.div`
  display: flex;
  flex-direction: center;

  justify-content: center;

  color: #ccc6ce;

  h1 {
    font-size: 3rem;
    margin: 0 0 3rem;
  }

  table {
    border-radius: 0.8rem;
    background: rgba(0, 0, 0, 0.5);
    border: 2px solid #333;

    margin-bottom: 2.5rem;

    padding: 1.6rem;
    font: 1.1rem Archivo;
  }

  td {
    display: flex;
    align-items: center;
    font-size: 2rem;
    padding: 1rem;

    max-width: 40rem;
    svg {
      margin-left: 1rem;
    }
  }

  a {
    text-decoration: none;
    color: #ccc6ce;
    font-size: 1.5rem;

    &:hover {
      color: #fe2e2e;
    }
  }
`;
